<template>
	<view id="login">
		
		<image class="background" src="../../static/login/background.jpg" ></image>
		<view  class="content">
		      <el-form ref="loginFormRef" label-width="100rpx"
			   :rules="loginFormRules" :model="loginForm" class="form">
		        <!-- 用户名 -->
		        <el-form-item prop="account" label="账号" size="medium">
		          <el-input v-model="loginForm.account"></el-input>
		        </el-form-item>
		        <!-- 密码 -->
		        <el-form-item prop="password" label="密码" size="medium">
		          <el-input v-model="loginForm.password" type="password"></el-input>
		        </el-form-item>
		      </el-form>
			  
		<view class="register" @click="register">
			注册账号
		</view>
			  
		<view class="handleLogin" @click="handleLogin">
		    <text>登</text>录
		</view>
		
		</view>
		</view>
	</view>
</template>

<script>
    import { login } from '../../service/login.js'
	
	export default {
		data() {
			return {
			loginForm:{
			account:"123"	,
			password:"123456"
			 },
			 loginFormRules: {
			        // 验证用户名是否合法
			    account: [
			        { required: true, message: '请输入登录账号', trigger: 'blur' },
			        { min: 0, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
			        ],
			        // 验证密码是否合法
			        password: [
			         { required: true, message: '请输入登录密码', trigger: 'blur' },
			         { min: 0, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
			    ]
			}
			}
		},
		onLoad() {
           
		},
		methods: {
		register(){
			uni.navigateTo({
				url:"../register/register"
			})
		},
		handleLogin(){
			    this.$refs.loginFormRef.validate(async valid => {
					/*uni.setStorage({
						key: 'username',
						data:this.$data.loginForm.username,
					});
					uni.setStorage({
						key: 'password',
						data:this.$data.loginForm.password,
					});*/
				const {account,password} = this.$data.loginForm
				
				login(this.$data.loginForm).then(res => {
					uni.setStorage({
						key: 'token',
						data:res.token,
					})
				})
				
				uni.navigateTo({
					url:"../home/home"
				})
			  /*  uni.request({
			        url: `http://localhost:8000/login`, //仅为示例，并非真实接口地址。
					method:'POST',
					data:{
						account:account,
						password:password
					},
					header: {
						'Content-Type': 'application/x-www-form-urlencoded'  
					}, 
					success: (res) => {
			              console.log(res.data);
			        }
				})*/
		  })
		}
	  }
	}
</script>

<style>
@import url('element-ui/lib/theme-chalk/index.css');
#login{
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
	z-index: 9;
}
#login .background{
	position: fixed;
	height: 100%;
	width: 100%;
	z-index: -1;
}

.content{
	position: absolute;
	top: 45%;
	left: 50%;
	transform: translate(-50%,-50%);
    color: #fff;
    font-size: 38rpx;
}

.handleLogin{
	height: 80rpx;
	line-height: 80rpx;
	margin-top: 150rpx;
	width: 600rpx;
	border: 2px solid #fff;
	border-radius: 20rpx;
	text-align: center;
	color:#fff;
	font-size: 40rpx;
	font-weight: bold;
}
.handleLogin text{
	margin-right: 20rpx;
}
/deep/.el-input__inner {
    background-color: transparent !important;
	border: none;
	border-bottom: 2px solid #fff;
}
/deep/.el-form-item__label{
	padding: 0;
    text-align: left;
	color:#fff;
	font-size:34rpx;
	font-weight: bold;
}
.register{
    float: right;
    font-size: 28rpx;
	font-weight: bold;
	color: #fff;
	overflow: hidden;
}
</style>
